import React, { Component } from 'react';
import '../resource/pages/AboutAuthor.css';
import Image1 from '../resource/profile/1.jpg';
import Image2 from '../resource/profile/2.jpg';
import ProfilePicture from '../components/ProfilePicture';

const images = [Image1, Image2];

function createComponent() {
  return (
    <div className="profile-body">
      <div className="profile-details header">
        <ProfilePicture images={images} />
        <h3>Marl Bermudo</h3>
        <p>
          Senior Software Developer who&apos;s adept in various programming
          language currently living in Pampanga, Philippines.
        </p>
      </div>
    </div>
  );
}

class AboutAuthor extends Component {
  render() {
    return createComponent();
  }
}

export default AboutAuthor;
