import React, { Component } from 'react';
import { Navbar, Nav, NavbarBrand } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

const BrandNavigation = ({ item }) => (
  <LinkContainer to={item.link}>
    <NavbarBrand>{item.component}</NavbarBrand>
  </LinkContainer>
);

const OtherNavigation = ({ item }) => (
  <LinkContainer to={item.link}>
    <Nav.Link>{item.component}</Nav.Link>
  </LinkContainer>
);

function createNav(brand, navigationItems) {
  return (
    <Navbar bg="dark" variant="dark">
      {brand != null && <BrandNavigation item={brand} />}
      <Nav className="mr-auto">
        {navigationItems.map(item => (
          <OtherNavigation item={item} key={item.key} />
        ))}
      </Nav>
    </Navbar>
  );
}

class Navigation extends Component {
  render() {
    const { brand, navigationItems } = this.props;
    return createNav(brand, navigationItems);
  }
}

export default Navigation;
